.nav-wrapper {
	width: 100%;
	height: 2rem;
	font-size: 1.8rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: #dcdcdd;
	background-color: #1985a1;
	-webkit-transition-property: all;
	-o-transition-property: all;
	transition-property: all;
	-webkit-transition-duration: 0.2s;
	     -o-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	-webkit-transition-timing-function: ease-in-out;
	     -o-transition-timing-function: ease-in-out;
	        transition-timing-function: ease-in-out;
}
.nav-wrapper.open {
	height: 14rem;
}

.nav-wrapper .nav-button {
	display: none;
}
.nav-wrapper.responsive .nav-button {
	display: inline;
}

.nav-wrapper.responsive .nav-item-wrapper {
	height: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	overflow: hidden;
	-webkit-transition-property: all;
	-o-transition-property: all;
	transition-property: all;
	-webkit-transition-duration: 0.2s;
	     -o-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	-webkit-transition-timing-function: ease-in-out;
	     -o-transition-timing-function: ease-in-out;
	        transition-timing-function: ease-in-out;
}
.nav-wrapper.responsive .nav-menu-button-wrapper {
	background-color: #177993;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
}

.nav-wrapper.responsive .nav-menu-button-wrapper:hover {
	cursor: pointer;
}
.nav-wrapper.open .nav-item-wrapper {
	height: 14rem;
}

@media screen and (min-width: 767.98px) {
	.nav-wrapper {
		height: 3rem;
		position: fixed;
		left: 50%;
		width: 50rem;
		margin-left: -25rem;

		/*box-shadow: 0 -1px 3px 3px #dcdcdd;
		border-bottom-left-radius: 1.5rem;
		border-bottom-right-radius: 1.5rem;*/
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		background-color: transparent;
	}

	.nav-item-wrapper {
		width: 100%;
		height: 100%;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: stretch;
		    align-items: stretch;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		color: #dcdcdd;
		font-size: 1.2rem;
	}

	.nav-item {
		height: 2rem;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		width: 20%;
		background-color: #1985a1;
		-webkit-box-shadow: 0px 0px 2px 0.75px #dcdcdd;
		        box-shadow: 0px 0px 2px 0.75px #dcdcdd;
		font-weight: 600;
		-webkit-transition-property: all;
		-o-transition-property: all;
		transition-property: all;
		-webkit-transition-duration: 0.3s;
		     -o-transition-duration: 0.3s;
		        transition-duration: 0.3s;
	}

	.nav-item.hovered{
		cursor: pointer;
		-webkit-box-shadow: 0px 0px 2px 1px #dcdcdd!important;
		        box-shadow: 0px 0px 2px 1px #dcdcdd!important;
		height: 2.25rem!important;
		-webkit-transition-property: all;
		-o-transition-property: all;
		transition-property: all;
		-webkit-transition-duration: 0.3s;
		     -o-transition-duration: 0.3s;
		        transition-duration: 0.3s;
		-webkit-transition-delay: ease-in-out;
		     -o-transition-delay: ease-in-out;
		        transition-delay: ease-in-out;
	}
  .nav-item.notHovered {
    height: 1.75rem;
    -webkit-box-shadow: 0px 0px 2px 1.25px #dcdcdd;
            box-shadow: 0px 0px 2px 1.25px #dcdcdd;
  }

}



